class Access extends Component {

  constructor(props){
    super(props);

    this.state={
        redirectToReferrer: false,
        termscondition : false,
        signin: true,
        data : {
            //login values
            l_name : "",
            l_pass : "",
        },
        loginError : {
            email : "",
            password    : ""
        },
    }
  }

  endOverlay = () => {
    if(this.state.overlay){
      this.setState({overlay:false})
      document.getElementById("overlay").style.display = "none";
    }
  } 

  initOverlay = (message) => {
      if(!this.state.overlay){
        this.setState({overlay:true})
        document.getElementById("loader-message").innerText = message
        document.getElementById("overlay").style.display = "block";
      }
  }

  errorDialog = (message)=> {
    Dialog.setOptions({
      defaultOkLabel: 'Ok'
    })
    this.dialog.show({
      body: message,
      actions: [
        Dialog.OKAction(
          () => {
            //Do nothing
          }
        )
      ]
    })
  }

  onSubmit = () =>{
        let emailError = "", passwordError = "";
        let loginError = this.state.loginError;
        const email = document.getElementById("standard-email").value;
        const password = document.getElementById("standard-password").value;

        emailError = Validate.util.validateEmail(email);
        passwordError = Validate.util.validatePassword(password);

        if( emailError != 1 ){
            this.errorDialog(emailError)
        }else if( passwordError != 1 ){
            this.errorDialog(passwordError)
        }else{
            this.initOverlay("Login in to your dashboard.");
            const timeout = setTimeout(() => {
              this.endOverlay();
              if( email == "gourav@growhospital.com" && password == "qwerty12345"){
                this.props.onSuccess({
                  status : true
                });
              }else{
                this.errorDialog("Incorrect Email or Passowrd");
              }
            }, 500);
            
            // agent.Auth.login(email,password).then(data => {
            //     const encryptedString = cryptr.encrypt(JSON.stringify({u:email,p:password}));
            //     this.endOverlay();
            //     try{
            //         if( data.data.status == 1 ){

            //                 const userObject = data.data.result;

            //                 //update data to local
            //                 localStorage.USER.updtaeUserAccess(
            //                     userObject["id"],
            //                     userObject["name"],
            //                     userObject["email"],
            //                     userObject["mobile"],
            //                     userObject["picture"],
            //                 );

            //                 //update the data to Redux
            //                 this.props.updateUser(userObject);

            //                 //Login in to your dashboard
            //                 this.props.onSuccess(userObject);

            //         }else if( data.data.status == 0 ){
            //                 console.log(data.data)
            //                 this.errorDialog(data.data.message);
            //         }
            //     }catch(e){
            //         this.errorDialog("Could not Sign In");
            //     }
            // });	
        }
  }

  handleChange = (event, type)=>{
    let data = this.state.data;
    data[type] = event.target.value;
    this.setState({
        data : data
    })
  }

  handleEnter = (e) => {
    if( e.keyCode == 13){
        if( this.state.signin ){
            this.onSubmit('signin')
        }else if( this.state.signup ){
            this.onSubmit('signup')
        }else if( this.state.termscondition ){
            this.handleRegisteration("signin")
        }
    }
  }

  promptDialog = (title,body,placeholder,type) => {
    this.dialog.show({
      title: title,
      body: body,
      actions: [
        Dialog.CancelAction(
          () => {

          }
        ),
        Dialog.OKAction(
          (el) => {
            console.log(el.value)
            if(type == 'otp'){
              this.verifyOTP(el.value);
            }
          }
        )
      ],
      bsSize: 'small',
      prompt: {initialValue: '', placeholder: placeholder},
      onHide: (dialog) => {
        dialog.hide()
        console.log('closed by clicking background.')
      }
    })
  }

  alertDialog = (title,body) => {
    Dialog.setOptions({
      defaultOkLabel: 'Ok'
    })
    this.dialog.show({
      title: title,
      body: body,
      actions: [
        Dialog.OKAction(
          (el) => {

          }
        )
      ],
      bsSize: 'small',
      onHide: (dialog) => {
        dialog.hide()
      }
    })
  }

  successDialog = (message) => {
    Dialog.setOptions({
      defaultOkLabel: 'Ok'
    })
    this.dialog.show({
      body: message,
      actions: [
        Dialog.OKAction(
          () => {

          }
        )
      ]
    })
  }

  render() {

    let loginBtn, forgotPassword, createAccount,entryFields , socialLogin, loginHeader, tncdiv;
    let dataDiv, mainDiv;

    let loginButtonDisabled = {
        background : "#e3e3e3",
        color : "black"
    }
    let loginButtonEnabled = {
        background : "#2196F3",
        color : "white"
    }
    if( this.state.signin ){
        const _emailEr=false,_passEr=false,_emailErVal="",_passErVal="";
        if(this.state.loginError.email != ""){
            _emailEr = true;
            _emailErVal = this.state.loginError.email;
        }
        if(this.state.loginError.password != ""){
            _passEr = true;
            _passErVal = this.state.loginError.password;
        }
        //Define the inner widgets
        loginHeader = <div className="main-header-login">User Login</div>

        // Button
        if( this.state.data.l_name !== "" && this.state.data.l_pass !== "" ){
            loginBtn = <Button 
                            className="signup-btn" 
                            style={loginButtonEnabled}
                            onClick={()=>this.onSubmit('signin')}
                            bsStyle="primary"
                            bsClass="secondary">
                            Login
                        </Button>
        }else{
            loginBtn = <Button 
                            className="signup-btn" 
                            style={loginButtonDisabled}
                            disabled = {true}
                            // onClick={()=>this.onSubmit('signin')}
                            bsStyle="primary"
                            bsClass="secondary">
                            Login
                        </Button>
        }
        //Text Entry Field
        entryFields = <div>
                        <div>
                            <TextField
                                    id="standard-email"
                                    label="Email Address"
                                    error={ _emailEr }
                                    onChange={(e) => this.handleChange(e,'l_name')}
                                    helperText = { _emailErVal }
                                    margin="normal"
                                    className="input-field"
                                    inputProps={{
                                        style: {fontSize: 15} 
                                    }}
                                />
                          </div>
                        <div>
                            <TextField
                                    id="standard-password"
                                    label="Password"
                                    error={ (this.state.loginError.password != "") ? true :false }
                                    helperText = { (this.state.loginError.password != "") ? this.state.loginError.password :"" }
                                    onChange={(e) => this.handleChange(e,'l_pass')}
                                    margin="normal"
                                    type="password"
                                    className="input-field"
                                    inputProps={{
                                        style: {fontSize: 15} 
                                    }}
                                />
                         </div>
                    </div>

        //Define the main widget
        mainDiv = <div className="access-wrapper">
                        <div className="login-div">
                            {loginHeader}
                            {createAccount}
                            {entryFields}
                            {loginBtn}
                        </div>
                    </div>
    }

    if(this.state.redirectToReferrer){
        return (<Redirect to={'/access'}/>);
    }

    return (
      <div className="main" onKeyUp={this.handleEnter} >
            <div id="overlay">
                <Loader 
                    className="loader-div"
                    type="Puff"
                    color="#00BFFF"
                />   
                <div id="loader-message" style={{color:'#FFFFFF',fontSize : '1.3vw'}}>Loading</div>
            </div>
            {mainDiv}
            <Dialog ref={(el) => { this.dialog = el }} />
      </div>
    )
  }
}

Access.propTypes = {
    classes: PropTypes.object.isRequired,
  };

const mapStateToProps = (state) => {
    return {
        sessionData : state.session,
        user : state.user 
    };
}

function mapDispatchToProps(dispatch) {
    return {
        updateUser: (userObject) => {
            dispatch({type: 'UPDATE_USER',payload : { user : userObject }})
        }
    };
}
  
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Access));  

