import React, { Component } from 'react';
import CheckoutItemCard from "./../CheckoutItemCard/CheckoutItemCard.jsx";
import {Table, Button, Modal} from 'react-bootstrap';
import { Redirect } from 'react-router-dom';
import Icon from 'react-icons-kit';
import "./Checkout.css";

class Checkout extends Component {
  constructor(props){
      super(props);
      console.log("Data from home : ",this.props.location.state.data);
      this.state = {
          itemLookup : this.props.location.state.data.itemLookup,
          vendorLookup : this.props.location.state.data.vendorLookup,
          confirmationModel : false,
          renderHome : false
      }
  }

  moveToHome = () => {
    this.setState({
        renderHome : true
    })
  }
  renderHome = () => {
    if (this.state.renderHome) {
        return (<Redirect to={{ pathname: '/'}} />)
      }
  }

  updateConfirmationVisibility = () => {
      if(this.state.confirmationModel){
           this.setState({
                confirmationModel : false
           })
      }else{
            this.setState({
                confirmationModel : true
            })
      }
  }

  confirmtionShow = () => {
        if(this.state.confirmationModel){

            const thankYouMessage = {
                height : "100px",
                textAlign : "center",
                fontSize : "1.1vw"
            }
            const continuePurchase = {
                height : "50px",
                textAlign : "center",
                fontSize : "1.1vw",
                paddingLeft : "30%",
                paddingRight : "30%"
            }
            const itemNameCol = {
                textAlign : "start"
            }
            let checkoutItemList = [];
            let tableColumnForBilling = [];
            let finalBillingPrice = 0;
            const itemList = this.state.itemLookup;
            //Collecting Items to show on order screen
            for(const itemId in itemList){
                const itemProcure = itemList[itemId].procure;
                let addItemForBilling = false;
                let totalItemPrice = 0;
                let totalItemQty = 0;
                for(let vendorId in itemProcure){
                    if( itemProcure[vendorId].qty > 0 ){
                        addItemForBilling = true;
                        totalItemQty += parseFloat(itemProcure[vendorId].qty);
                        totalItemPrice += parseFloat(itemProcure[vendorId].qty)* parseFloat(itemProcure[vendorId].price);
                    }
                }
                if( addItemForBilling ){
                    checkoutItemList.push(
                        <div className="col-xs-12 checkout-item-card-div">
                              <CheckoutItemCard
                                item = {itemList[itemId]}
                                vendorLookup = {this.state.vendorLookup}
                              />
                        </div>
                    )
                }
                if( totalItemPrice > 0 ){
                    finalBillingPrice += totalItemPrice;
                    tableColumnForBilling.push(
                                <tr>
                                    <td style={itemNameCol}>{itemList[itemId].name}</td>
                                    <td>{totalItemQty}</td>
                                    <td>Rs. {totalItemPrice}</td>
                                </tr>
                    )
                }
            }
            return <Modal
                        show={this.state.confirmationModel}
                        onHide={() => this.updateConfirmationVisibility()}
                        dialogClassName="modal-90w"
                        aria-labelledby="example-custom-modal-styling-title" >
                            
                        <Modal.Header closeButton>
                        <Modal.Title id="example-custom-modal-styling-title">
                            Confirmation
                        </Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <div style={thankYouMessage}>
                                Thank you for purchasing from <b>GrowYourHospital</b>, we have sent a confirmation email to you.
                                Please find the order details below.
                            </div>
                                <Table bordered hover size="sm">
                                    <thead>
                                        <tr>
                                            <th>Item</th>
                                            <th>Qty</th>
                                            <th>Price</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {tableColumnForBilling}
                                        <tr>
                                            <td colSpan="2">Total</td>
                                            <td>Rs. {finalBillingPrice}</td>
                                        </tr>
                                    </tbody>
                                </Table>
                            <div>Order date</div>
                                 <div><b>{ (new Date()).toLocaleDateString() }</b></div>
                            <div style={continuePurchase}>
                                <div onClick={this.moveToHome} className="checkout-btn">
                                    Continue Purchase
                                </div>
                            </div>
                        </Modal.Body>
                    </Modal>
        }
  }

  render() {
    const headerDiv = {
        height : "7%",
        width : "100%",
        textAlign : "start",
        fontSize : "1.45vw"
    }
    const resultArea = {
        height : "100%",
        width : "100%",
        float : "left"
    }
    const orderListDiv = {
        height : "92%",
        width : "100%",
        maxHeight : "92%",
        overflowY: "scroll"
    }
    const orderSummary = {
        height : "92%",
        width : "100%",
        paddingTop : "2%"
    }
    const nameHeader = {
        height : "100%",
        width : "50%",
        float : "left"
    }
    const iconStyle = {
        marginLeft : "5%"
    }
    const confirmCheckoutBtn = {
        paddingLeft : "20%"
    }
    const noItemStyle = {
        fontSize : "1.3vw",
        marginTop : "5%"
    }
    const itemNameCol = {
        textAlign : "start"
    }
    let checkoutItemList = [];
    let tableColumnForBilling = [];
    let finalBillingPrice = 0;
    const itemList = this.state.itemLookup;
    //Collecting Items to show on order screen
    for(const itemId in itemList){
        const itemProcure = itemList[itemId].procure;
        let addItemForBilling = false;
        let totalItemPrice = 0;
        let totalItemQty = 0;
        for(let vendorId in itemProcure){
            if( itemProcure[vendorId].qty > 0 ){
                addItemForBilling = true;
                totalItemQty += parseFloat(itemProcure[vendorId].qty);
                totalItemPrice += parseFloat(itemProcure[vendorId].qty)* parseFloat(itemProcure[vendorId].price);
            }
        }
        if( addItemForBilling ){
            checkoutItemList.push(
                <div className="col-xs-12 checkout-item-card-div">
                      <CheckoutItemCard
                        item = {itemList[itemId]}
                        vendorLookup = {this.state.vendorLookup}
                      />
                </div>
            )
        }
        if( totalItemPrice > 0 ){
            finalBillingPrice += totalItemPrice;
            tableColumnForBilling.push(
                        <tr>
                            <td style={itemNameCol}>{itemList[itemId].name}</td>
                            <td>{totalItemQty}</td>
                            <td>Rs. {totalItemPrice}</td>
                        </tr>
            )
        }
    }
    if( checkoutItemList.length == 0 ){
        checkoutItemList = <div style={noItemStyle}>No item added</div>
    }
    return (
      <div className="checkout-main">
        {this.renderHome()}
        {this.confirmtionShow()}
        <div className="col-xs-8 full-height checkout-item-list">
            {/* Header Area */}
            <div style={headerDiv}>
                <div style={resultArea}>Your Orders</div>
            </div>
            <div className="divider-line"></div>
            <div style={orderListDiv}>
                {checkoutItemList}
            </div>
        </div>
        <div className="col-xs-4 full-height">
            {/* Header Area */}
            <div style={headerDiv}>
                <div style={nameHeader}>Order Summary</div>
                <div style={nameHeader}>
                    <div className="checkout-btn" style={confirmCheckoutBtn} onClick={()=>{this.updateConfirmationVisibility()}}>Confirm Checkout</div>
                </div>
            </div>
            <div className="divider-line"></div>
            <div style={orderSummary}>
                <Table bordered hover size="sm">
                    <thead>
                        <tr>
                            <th>Item</th>
                            <th>Qty</th>
                            <th>Price</th>
                        </tr>
                    </thead>
                    <tbody>
                        {tableColumnForBilling}
                        <tr>
                            <td colSpan="2">Total</td>
                            <td>Rs. {finalBillingPrice}</td>
                        </tr>
                    </tbody>
                </Table>
            </div>
        </div>
      </div>
    )
  }
}
export default Checkout;