import React, { Component } from 'react';
import { Image } from 'react-bootstrap';
import Icon from 'react-icons-kit';
import {tag} from 'react-icons-kit/fa/tag'
import './VendorCard.css';

class VendorCard extends Component {

  addQtyByOne = () => {
    this.props.update({
        vendorId : this.props.id,
        price : this.props.price,
        operation : "add",
        number : 1
    })
  }
  removeQtyByOne = () => {
    this.props.update({
        vendorId : this.props.id,
        price : this.props.price,
        operation : "remove",
        number : 1
    })
  }
  updateQty = (event) => {
    this.props.update({
        vendorId : this.props.id,
        price : this.props.price,
        operation : "update",
        number : event.target.value
    })
  }
  render() {
    const vendorDetail = {
        height : "25%",
        width : "100%",
        marginBottom : "10px"
    }
    const vendorLogo = {
        height : "100%",
        width : "15%",
        float : 'left'
    }
    const vendorName = {
        height : "100%",
        width : "80%",
        float : 'left',
        textAlign : "start",
        paddingTop : "3%",
        paddingLeft : "2%",
        fontSize : "1vw",
        fontWeight : "bold"
    }
    const imageSize = {
        maxWidth:'100%',
        maxHeight:'100%',
        border : "1px solid #E0E0E0"
    }
    const brandDetail = {
        height : "10%",
        width : "100%",
        marginBottom : "5px",
        fontSize : "0.9vw"
    }
    const brandName = {
        textAlign : "start",
        paddingLeft : "2%",
    }
    const descriptionDiv = {
        height : "25%",
        width : "100%",
        marginBottom : "5px",
        fontSize : "0.8vw"
    }
    const priceDivider = {
        width: "100%",
        height: "1px",
        background: "#E0E0E0",
    }
    const priceDetail = {
        height : "25%",
        width : "100%",
        fontSize : "1vw",
    }
    const priceDiv = {
        height : "100%",
        textAlign : "start",
        paddingTop : "4px",
    }
    const priceValue = {
        fontSize : "1vw",
        width : "100%",
        height : "60%",
        fontWeight : "bold"
    }
    const priceSubDetail = {
        fontSize : "0.6vw",
        width : "100%",
        height : "30%"
    }
    const addBtnStyleLeft = {
        height : "100%",
        width : "25%",
        float : "left",
        borderRadius : "5px 0 0 5px",
        cursor : "pointer",
        background : "#66BB6A",
        fontSize : "1.5vw",
        textAlign : "center",
        color : "white"
    }
    const addBtnStyleMid = {
        height : "100%",
        border : "1px solid #ddd",
        width : "50%",
        float : "left"
    }
    const addBtnStyleRIght = {
        height : "100%",
        width : "25%",
        float : "left",
        borderRadius : "0 5px 5px 0",
        cursor : "pointer",
        background : "#66BB6A",
        fontSize : "1.5vw",
        textAlign : "center",
        color : "white"
    }

    let vendorCardMain = {
        height: "100%",
        width: "100%",
        borderRadius: "7px",
        border: "1px solid #E0E0E0",
        padding: "2%",
    }

    const collectData = this.props.collectData;
    const collectQty = collectData.qty;

    if(collectQty == undefined || collectQty == 0 ){
        vendorCardMain.border = "1px solid #E0E0E0";
    }else{
        vendorCardMain.border = "2px solid #66BB6A";
    }

    const readMore = <span className="read-more">...more</span>;
    const itemDescription = this.props.desc;
    let itemDescriptionText;
    if( itemDescription.length > 90){
        itemDescriptionText = itemDescription.substr(0,80)
    }else{
        itemDescriptionText = itemDescription
    }
    return (
      <div style={vendorCardMain}>
        <div style={vendorDetail}>
                {/* Vendor name */}
                <div style={vendorLogo}><Image src="/assets/vendor.png" style={imageSize}/></div>
                {/* Vendor logo */}
                <div style={vendorName}>{this.props.name}</div>
        </div>
        <div style={brandDetail}>
                {/* Brand Detail */}
                <div style={brandName}><b>Brand</b>: {this.props.brand}</div>
        </div>
        <div style={descriptionDiv}>
                {/* Brand Detail */}
                <div style={brandName}><b>Description</b>:  {itemDescriptionText} {itemDescription.length>90?readMore:""} </div>
        </div>
        <div style={priceDivider}></div>
        <div style={priceDetail}>
             <div style={priceDiv} className="col-xs-6">
                <div style={priceValue}>Rs. {this.props.price}</div>
                <div style={priceSubDetail}>(price per qty)</div>
             </div>
             <div style={priceDiv} className="col-xs-6">
                 <div style={addBtnStyleLeft} onClick={this.removeQtyByOne}> - </div>
                 <div style={addBtnStyleMid}> <input minLength="0" onChange={this.updateQty} className="inputStyle" value={collectQty==undefined?0:collectQty} type="number"/> </div>
                 <div style={addBtnStyleRIght} onClick={this.addQtyByOne}> + </div>
             </div>
        </div>
      </div>
    )
  }
}
export default VendorCard;