import React, { Component } from 'react';
import "./ItemCard.css";

class Itemcard extends Component {
  clickItem = () => {
    this.props.click({
      id : this.props.id
    })
  }

  render() {
    let boxStyle = {
        height: "100%",
        width: "100%",
        borderRadius: "5px",
        padding: "2%",
        cursor: "pointer"
    }
    if( this.props.selected ){
      boxStyle["border"] = "2px solid #42A5F5";
      boxStyle["boxShadow"] = "0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2)"
    }else{
      boxStyle["border"] = "1px solid #E0E0E0";
    }

    let textStyle = {
        width : "100%",
        height : "100%",
        textAlign : "Start",
        paddingTop : "2%",
        paddingLeft : "2%",
        fontSize : "0.9vw",
        fontWeight : "bold",
    }
    const procure = this.props.procure;
    let checked = false;
    for(let vendorId in procure){
      if( procure[vendorId].qty > 0 ){
        checked = true
      }
    }
    if(checked){
      textStyle.color = "#66BB6A";
    }else{
      textStyle.color = "#546E7A";
    }
    return (
      <div style={boxStyle} onClick={this.clickItem} >
          <div style={textStyle}>{this.props.name}</div>
      </div>
    )
  }
}
export default Itemcard;