import axios from 'axios';
import qs from 'qs';

//This will check, which urls to use
const develop = false;

const CLOUD = {
	LOCAL : {
		API_ROOT : 'http://localhost:8080/',
	},
	SERVER : {
		API_ROOT : 'http://3.86.155.41:8080/',
	}
}

let URL = {};
if( develop ){
	URL = Object.assign({}, CLOUD.LOCAL )
}else{
	URL = Object.assign({}, CLOUD.SERVER )
}

const authToken = "fe5c171ac720d5145cfcf5170eb631946e6f4c48";
axios.defaults.headers.common['Authorization'] = `Token ${authToken}` 

const requests = {
	get : (url) => {
		return axios.get(URL.API_ROOT + url)
					.then(res => {
						return res;
					})
					.catch(function (error) {
					    console.log(error);
					})
	},
	post : (url ,body) => {
		return axios.post(URL.API_ROOT + url, body)
			.then(res => {
				return res;
			})
			.catch(function (error) {
			    console.log(error);
			 })
	}
}

const Procure = {
	itemList : () => {
		return requests.get('procure/items');
    },
    vendorList : () => {
		return requests.get('procure/vendors');
    },
    vendorItemList : () => {
		return requests.get('procure/vendor-items');
    },
}

export default {
    Procure
}