import React, { Component } from 'react';
import "./CheckoutItemCard.css";

class CheckoutItemCard extends Component {
  render() {
    const cardHeader = {
        height: "50px",
        background : "#eee",
        borderRadius: "10px 10px 0px 0px",
    }
    const cardItem = {
        height: "30px",
    }
    const ItemNameHeader = {
        fontSize : "1.1vw",
        textAlign : "start",
        paddingTop : "1.5%",
        color : "#455A64",
        fontWeight : "bold"
    }
    const vendorNameHeader = {
        fontSize : "0.9vw",
        textAlign : "start",
        paddingTop : "1.5%",
        color : "#455A64",
    }
    
    const itemData = this.props.item;
    let totalPrice = 0;
    const vendorLookup = this.props.vendorLookup;

    //Adding the vendors data
    const procurement = itemData.procure;
    const vendorList = [];
    for(const vendorId in procurement){
        const procureData = procurement[vendorId];
        totalPrice += (parseFloat(procureData.price)*parseFloat(procureData.qty));
        vendorList.push(
            <div style={cardItem}>
              <div className="col-xs-10 full-height" style={vendorNameHeader}>{procureData.qty} x {vendorLookup[vendorId].name}</div>
              <div className="col-xs-2 full-height" style={vendorNameHeader}>Rs. { parseFloat(procureData.price)* parseFloat(procureData.qty)  }</div>
              {/* <div className="col-xs-2 full-height remove-item">Remove</div> */}
            </div>
        )
    }

    return (
      <div className="checkout-card-main">
          <div style={cardHeader}>
              <div className="col-xs-10 full-height" style={ItemNameHeader}>{itemData.name}</div>
              <div className="col-xs-2 full-height" style={ItemNameHeader}>Rs. {totalPrice}</div>
          </div>
          {vendorList}
      </div>
    )
  }
}
export default CheckoutItemCard;