import React, { Component } from 'react';
import VendorCard from './../VendorCard/VendorCard.jsx';
import ItemCard from "./../ItemCard/ItemCard.jsx"
import {shoppingCart} from 'react-icons-kit/fa/shoppingCart';
import Icon from 'react-icons-kit';
import agent from '../../services/agent.js';
import {search} from 'react-icons-kit/fa/search'
import { Redirect } from 'react-router-dom';
import Loader from 'react-loader-spinner';
import './Home.css';

class Home extends Component {

  constructor(props){
      super(props);
      this.state = {
          vendorLookup : null,
          itemLookup : null,
          vendorItemLookup : null,
          load : false,
          selectedItem : null,
          proceedToCheckout : false
      }
  }
  
  componentDidMount = () => {
    this.initOverlay("Fetching the data");
    this.fetchItems();
    this.fetchVendorItem();
    this.fetchVendors();
  }

  fetchVendors = () => {
    try{
      agent.Procure.vendorList()
      .then(data => {
          let vendorList = {}
          data.data.result.forEach( (d) => {
            vendorList[ d.pk ] = d.fields
            vendorList[ d.pk ].id = d.pk
          })
          this.setState({
            vendorLookup : vendorList
          },()=>{
              this.collectVendor();
          })
      })
    }catch(e){
          console.log("Could not fetch the subject data.")
    }
  }

  fetchVendorItem = () => {
    try{
      agent.Procure.vendorItemList()
      .then(data => {
          let vendorItemList = {}
          data.data.result.forEach( (d) => {
            vendorItemList[ d.fields.item +"_"+ d.fields.vendor ] = d.fields
          })
          this.setState({
            vendorItemLookup : vendorItemList
          },()=>{
              this.collectVendor();
          })
      })
    }catch(e){
          console.log("Could not fetch the subject data.")
    }
  }

  fetchItems = () => {
    try{
      agent.Procure.itemList()
      .then(data => {
          let itemList = {}
          data.data.result.forEach( (d) => {
            itemList[ d.pk ] = d.fields
            itemList[ d.pk ].id = d.pk
            itemList[ d.pk ].vendor = []
            itemList[ d.pk ].procure = {}
          })
          this.setState({
            itemLookup : itemList
          },()=>{
              this.collectVendor();
          })
      })
    }catch(e){
          console.log("Could not fetch the subject data.")
    }
  }

  collectVendor = () => {
        if( this.state.itemLookup !== null 
            && this.state.vendorLookup !== null 
            && this.state.vendorItemLookup !== null){
            //Arrange the data so that items have vendors list
            // Get the key list in Items
            // Iterate through vendor to find vendors corresponding to item
            // Add vendors to items
            //const itemKeys = this.state.itemLookup

            //End the overlay
            this.endOverlay();

            let itemLookup = this.state.itemLookup;
            const vendorItemLookup = this.state.vendorItemLookup;
            for(let vendorItem in vendorItemLookup){
                let itemId = vendorItemLookup[vendorItem].item;
                let vendorId = vendorItemLookup[vendorItem].vendor;
                if( itemLookup[itemId].vendor == undefined ){
                    itemLookup[itemId].vendor = []
                }
                itemLookup[itemId].vendor.push(vendorId)
            }
            this.setState({
                itemLookup : itemLookup,
                load : true
            })
        }
  }

    selectItem = (_State) => {
        console.log(_State);
        this.setState({
            selectedItem : _State.id,
        })
    }

    updateItem = (_State) => {
        //Get the Item Id
        const itemId = this.state.selectedItem;
        const vendorId = _State.vendorId;
        const price = _State.price;
        let itemLookup = this.state.itemLookup;
        let item = itemLookup[itemId];

        let procurementData = item.procure;
        if( procurementData[vendorId] == undefined ){
            if( _State.operation == "add" || _State.operation == "update" ){
                procurementData[vendorId] = {
                    vendor : vendorId,
                    price : price,
                    qty : _State.number
                }
            }
        }else{
            let previousQty = procurementData[vendorId].qty;
            if( _State.operation == "add" ){
                previousQty += 1
            }else if( _State.operation == "remove" ){
                if( previousQty > 0 ){
                    previousQty -= 1
                }
            }else if( _State.operation == "update" ){
                previousQty = _State.number;
            }
            procurementData[vendorId].qty =  parseFloat(previousQty) ;
        }
        //Update the item
        item.procure = procurementData;

        //Update the data to itemLookup
        itemLookup[itemId] = item;

        //Update the lookup to state
        this.setState({
            itemLookup : itemLookup,
        },()=>{
            console.log(this.state.itemLookup)
        })
    }

  endOverlay = () => {
    if(this.state.overlay){
      this.setState({overlay:false})
      document.getElementById("overlay").style.display = "none";
    }
  } 

  initOverlay = (message) => {
      if(!this.state.overlay){
        this.setState({overlay:true})
        document.getElementById("loader-message").innerText = message
        document.getElementById("overlay").style.display = "block";
      }
  }

  moveToCheckout = () => {
      this.setState({
        proceedToCheckout : true
      })
  }

  renderCheckout = () => {
    if (this.state.proceedToCheckout) {
      return (<Redirect to={{
                  pathname: '/checkout',
                  state: { data: { itemLookup:  this.state.itemLookup , vendorLookup : this.state.vendorLookup } }
              }} />)
    }
  }

  render() {
    const headerDiv = {
        height : "7%",
        width : "100%",
        textAlign : "start",
        fontSize : "1.45vw"
    }
    const resultArea = {
        height : "100%",
        width : "85%",
        float : "left"
    }
    const checkoutArea = {
        height : "100%",
        width : "15%",
        float : "left",
    }
    const iconStyle = {
        marginLeft : "5%"
    }
    const vendorListDiv = {
        height : "92%",
        width : "100%",
        maxHeight : "92%",
        overflowY: "scroll"
    }
    const itemListDiv = {
        height : "92%",
        width : "100%",
        maxHeight : "92%",
        overflowY: "scroll"
    }
    const searchIconStyle = {
        marginLeft : "5%",
        color : "#1976D2",
        cursor : "pointer"
    }
    const checkoutBtnStyle = {
        paddingLeft : "20%"
    }

    //Display the list of items
    let itemCardList = [];
    const itemObject = this.state.itemLookup;
    if(this.state.load){
        for(let itemId in itemObject){
            let item = itemObject[itemId];
            let selected = false;
            if( itemId == this.state.selectedItem ){
                selected = true;
            }
            itemCardList.push(
                <div className="col-xs-12 item-card-div" key={item.id}>
                      <ItemCard
                        id = {item.id}
                        name = {item.name}
                        procure = {item.procure}
                        selected = {selected}
                        click = {this.selectItem}
                      />
                  </div>
            )
        }
    }

    //Display the vendors list
    const noItemStyle = {
        fontSize : "1.3vw",
        marginTop : "5%"
    }
    let vendorCardList = [];
    let selectedItemName = "";
    if(this.state.selectedItem == null){
        vendorCardList = <div style={noItemStyle}>No item selected</div>
    }else{

        //Update the selected Name
        selectedItemName = this.state.itemLookup[this.state.selectedItem].name;
        const vendorIdList = this.state.itemLookup[this.state.selectedItem].vendor;
        if( vendorIdList.length == 0 ){
            vendorCardList = <div style={noItemStyle}>No vendor available</div>
        }else{
            vendorIdList.forEach((vendorId)=>{
                let vendor = this.state.vendorLookup[vendorId];
                //Collect the procurement data
                let vendorItem = this.state.vendorItemLookup[this.state.selectedItem+"_"+vendorId]

                //Get the previous qty selected
                let vendorCollectData = {};
                const vendorQty = this.state.itemLookup[this.state.selectedItem].procure[vendorId]
                if( vendorQty != undefined ){
                    vendorCollectData = vendorQty;
                }

                vendorCardList.push(
                    <div className="col-xs-4 vendor-card-div" key={vendorId}>
                          <VendorCard
                            id = {vendorId}
                            name = {vendor.name}
                            price = {vendorItem.price}
                            brand = {vendorItem.brand}
                            desc = {vendorItem.description}
                            update = {this.updateItem}
                            collectData = {vendorCollectData}
                          />
                    </div>
                )
            })
        }
    }
    return (
      <div className="home-main">
          {this.renderCheckout()}
          <div id="overlay">
            <Loader 
                className="loader-div"
                type="Puff"
                color="#00BFFF"
            />   
            <div id="loader-message" style={{color:'#FFFFFF',fontSize : '1.3vw'}}>Loading</div>
          </div>
          <div className="col-xs-3 full-height item-div">
               {/* Header Area */}
               <div style={headerDiv}>
                  <div style={resultArea}>Item list   </div>
                  <div style={checkoutArea}>
                    {/* <Icon style={searchIconStyle} icon={search} ></Icon> */}
                  </div>
               </div>
               <div className="divider-line"></div>
               {/* Item List area */}
              <div style={itemListDiv}>
                 {itemCardList}
              </div>
          </div>
          <div className="col-xs-9 full-height vendor-div">
              {/* Header Area */}
              <div style={headerDiv}>
                  <div style={resultArea}>Search Results: {selectedItemName}</div>
                  <div style={checkoutArea}>
                      <div onClick={this.moveToCheckout} className="checkout-btn" style={checkoutBtnStyle}>Checkout <Icon style={iconStyle} icon={shoppingCart} ></Icon></div>
                  </div>
              </div>
              <div className="divider-line"></div>
              {/* Vendor List area */}
              <div style={vendorListDiv}>
                  {vendorCardList}
              </div>
          </div>
      </div>
    )
  }
}
export default Home;