import React, { Component } from 'react'
import { BrowserRouter as Router, Route ,Switch} from 'react-router-dom';
import CustomNavbar from './components/CustomNavbar/CustomNavbar.jsx'
import Access from './components/Access/Access.jsx';
import Home from './components/Home/Home.jsx';
import Checkout from "./components/Checkout/Checkout.jsx";
import './App.css';

class App extends Component {

  constructor(props){
    super(props);
    let loginAccessVal = 0;
    this.state = {
      loginAccess : loginAccessVal,
    }
  }

  updateAccessDetails = () => {
    this.setState({
      loginAccess : 1,
    })
  }

  onLogout = () => {
    this.setState({
      loginAccess : 0,
    })
  }

  render() {
    let pages = "";
    if(this.state.loginAccess == 0){
      pages =  <div className="App">
                  <Access onSuccess={this.updateAccessDetails}/>
              </div>
    }else if(this.state.loginAccess == 1){
      pages =  <div className="App-main">
                <div className="navbar-section">
                  <CustomNavbar
                    logout = {()=>this.onLogout()}
                  /> 
                </div>
                <div className="pages">
                      <Route
                            exact
                            path='/'
                            component={() => <Home/>}
                      />
                      <Route exact path="/checkout" component = {Checkout} />
                </div>
              </div>
    }
    return (
      <div style={{height:'100%',width:'100%'}}>
        <Router>
          {pages}
        </Router>
      </div>
    )
  }
}
export default App
